import { useState, useEffect } from "react";
import Layout from "../components/LayoutComponent";
import LastSummary from "../components/LastSummaryComponent";
import CombinedChart from "../components/CombinedChartComponent";
import SavingsPrediction from "../components/SavingsPredictionComponent";
import { IncomeData, ExpenseData } from "../interfaces/InterfaceData";
import AddTransactionForm from "../components/AddTransactionForm";

const Dashboard = () => {
  const [incomeData, setIncomeData] = useState<IncomeData[]>([]);
  const [expenseData, setExpenseData] = useState<ExpenseData[]>([]);
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState<number | "">("");
  const [transactionType, setTransactionType] = useState("income"); // Default: Pemasukan
  const [loading, setLoading] = useState(false);
  const savings = 500;

  useEffect(() => {
    const fetchIncomeData = async () => {
      try {
        const response = await fetch("/api/incomes");
        const data = await response.json();
        setIncomeData(data);
      } catch (error) {
        console.error("Error fetching income data:", error);
      }
    };

    const fetchExpenseData = async () => {
      try {
        const response = await fetch("/api/expenses");
        const data = await response.json();
        setExpenseData(data);
      } catch (error) {
        console.error("Error fetching expense data:", error);
      }
    };

    fetchIncomeData();
    fetchExpenseData();
  }, []);

  const handleAddTransaction = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!description || !amount) {
      alert("Deskripsi dan jumlah harus diisi!");
      return;
    }

    setLoading(true);

    const newTransaction = {
      description,
      amount: Number(amount),
      type: transactionType,
      date: new Date().toISOString(),
    };

    try {
      const response = await fetch("/api/transactions", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newTransaction),
      });

      if (!response.ok) {
        throw new Error("Gagal menambah transaksi");
      }

      const addedTransaction = await response.json();

      // Update list sesuai tipe transaksi
      if (transactionType === "income") {
        setIncomeData([...incomeData, addedTransaction]);
      } else {
        setExpenseData([...expenseData, addedTransaction]);
      }

      // Reset form
      setDescription("");
      setAmount("");
      setTransactionType("income"); // Kembali ke default pemasukan
    } catch (error) {
      console.error("Error adding transaction:", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Layout>
      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">
          Grafik Pemasukan dan Pengeluaran
        </h3>
        <CombinedChart incomeData={incomeData} expenseData={expenseData} />
      </div>

      <div className="mb-8">
        <LastSummary />
      </div>

      {/* Form Tambah Transaksi */}
      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">Tambah Transaksi</h3>
        <form onSubmit={handleAddTransaction} className="space-y-4">
          {/* Deskripsi */}
          <div>
            <label className="block text-lg font-medium">Deskripsi</label>
            <input
              type="text"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className="w-full p-2 border rounded-lg"
              required
            />
          </div>

          {/* Jumlah */}
          <div>
            <label className="block text-lg font-medium">Jumlah (Rp)</label>
            <input
              type="number"
              value={amount}
              onChange={(e) => setAmount(Number(e.target.value))}
              className="w-full p-2 border rounded-lg"
              required
            />
          </div>

          {/* Tipe Transaksi (Combo Box) */}
          <div>
            <label className="block text-lg font-medium">Tipe Transaksi</label>
            <select
              value={transactionType}
              onChange={(e) => setTransactionType(e.target.value)}
              className="w-full p-2 border rounded-lg"
            >
              <option value="income">Pemasukan</option>
              <option value="expense">Pengeluaran</option>
            </select>
          </div>

          <button
            type="submit"
            className="bg-blue-500 text-white px-4 py-2 rounded-lg"
            disabled={loading}
          >
            {loading ? "Menambah..." : "Tambah Transaksi"}
          </button>
        </form>
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">Prediksi Tabungan</h3>
        <SavingsPrediction savings={savings} />
      </div>
    </Layout>
  );
};

export default Dashboard;
